.global {
    margin: 0 auto;
    width: 880px;
}

label {
    padding-right: 5px;
}

@media only screen and (max-width: 880px) {
    .global {
        width: 100%;
    }

    .portfolios-container {
        display: flex;
        flex-flow: column;
        justify-content: center;
    }
}

.portfolio-form {
    margin: 15px auto;
    max-width: 320px;
}

.portfolios-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}

.portfolio-element {
    max-width: 450px;
    margin: 15px;
    border: 1px solid black;
}

.portfolio-header {
    display: flex;
    justify-content: space-between;
}

.portfolio-name {
    font-weight: bold;
    margin-left: 15px;
}

.portfolio-delete {
    margin: 15px;
}

.zui-table {
    text-align: center ;
    border: solid 1px #DDEEEE;
    border-collapse: collapse;
    border-spacing: 0;
    font: normal 12px Arial, sans-serif;
}
.zui-table thead th {
    background-color: #DDEFEF;
    border: solid 1px #DDEEEE;
    color: #336B6B;
    padding: 5px;
    text-align: left;
    text-shadow: 1px 1px 1px #fff;
}
.zui-table tbody td {
    border: solid 1px #DDEEEE;
    color: #333;
    padding: 5px;
    text-shadow: 1px 1px 1px #fff;
}

.total-sum {
    margin: 15px 15px 0px 15px;
}

.stock-form {
    margin: 15px;
}

.name-input-field, .symbol-input-field, .quantity-input-field {
    margin-right: 10px;
}

.symbol-input-field {
    width: 40px;
}

.quantity-input-field {
    width: 40px;
}
